import { Component } from '@angular/core';
import { Resume } from '../shared/model/resume/resume.model';
import { PortfolioData } from './portfolio.data';
import { Title } from '@angular/platform-browser';

@Component({
  selector: 'portfolio-component',
  templateUrl: './portfolio.component.html',
  styleUrls: ['./template.component.css', './portfolio.component.css']
})
export class PortfolioComponent {

  public resume: Resume;
  public color: string;

  constructor(titleService: Title) {
    this.resume = PortfolioData.generate();
    this.color = PortfolioData.getColor();
    titleService.setTitle(this.resume.basics.name);
  }
  W
  public scrollTo(element: string): false {
    document.getElementById(element).scrollIntoView({
      block: 'start',
      behavior: 'smooth'
    });
    return false;
  }
}
