import { Profile } from './profile.model';
import { Location } from './location.model';

export class Basics {

  // Schema
  name: string;
  label: string;
  picture: string;
  email: string;
  phone: string;
  website: string;
  summary: string;
  location: Location;
  profiles: Array<Profile>;
  // Extended
  title: string;
  firstName: string;
  lastName: string;

  public static setName(data: Basics, firstName: string, lastName: string = ''): void {
    data.firstName = firstName;
    data.lastName = lastName;
    data.name = firstName + (lastName != null && lastName.length > 0 ? ' ' + lastName : '');
  }

  static setTitle(data: Basics, title: string): void {
    data.title = title;
  }

  static setCity(basics: Basics, city: string): void {
    if (basics.location == null) {
      basics.location = new Location();
    }
    basics.location.city = city;
  }

  static setWebsite(basics: Basics, website: string): void {
    basics.website = website;
  }

  static setEmail(basics: Basics, email: string): void {
    basics.email = email;
  }
}
